import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable, Injector } from '@angular/core';
import { Observable } from 'rxjs';
import { BaseResourceModel } from '../models/base-resource.model';
import { IFilter } from '../models/interface.model';
import { environment as env } from "environments/environment";

@Injectable({
  providedIn: 'root'
})
export abstract class BaseResourceService<T extends BaseResourceModel, R> {
  protected http: HttpClient;

  constructor(
    protected apiPath: string,
    protected injector: Injector,
  ) {
    this.http = injector.get(HttpClient);
  }

  getAll(filter: IFilter): Observable<R> {
    const parametros = new HttpParams()
      .set('filtro', filter.filtro)
      .set('page', filter.pagina)
      .set('linesPerPage', filter.tamanho)
      .set('orderBy', filter.ordenarPor)
      .set('direction', filter.direcaoOrdenacao);
    return this.http.get<R>(`${env.api}${this.apiPath}/page`, {
      params: parametros,
    });
  }

  getById(id: number): Observable<T> {
    return this.http.get<T>(`${env.api}${this.apiPath}/${id}`);
  }

  insert(perfil: T): Observable<null> {
    return this.http.post<null>(`${env.api}${this.apiPath}`, perfil);
  }

  update(perfil: T): Observable<null> {
    return this.http.put<null>(
      `${env.api}${this.apiPath}/${perfil.id}`,
      perfil
    );
  }

  delete(id: number): Observable<null> {
    return this.http.delete<null>(`${env.api}${this.apiPath}/${id}`);
  }
}
